.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.retryEmoticon{
  font-size: 30px;
  cursor: pointer;
  
  
}

.gameHeader{
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-content: center;
  align-items: center;
}

.background-video{
  position: fixed;
  width:100vw;
  height: 100vh;
  z-index: -1; 
  object-fit: cover; 
  top: 0;
  left: 0;
}

.awsome-dev{
  font-size: larger;
  color: rgb(101, 199, 231);
  filter: brightness(200%);
}